import React from 'react';
import { BrowserRouter as Router, Route, Switch } from 'react-router-dom';
import { HomePage, AboutPage, ArticlePage, ArticlesListPage, NotFoundPage } from './pages';
import { NavBar } from './components';
import './App.css';

const add = (x, y) => x + y;

function App() {
  return (
    <Router>
      <div className="App">
        <NavBar />
        <div id="page-body">
          <Switch>
            <Route path="/" exact>
              <HomePage />
            </Route>
            <Route path="/about">
              <AboutPage />
            </Route>
            <Route path="/articles-list">
              <ArticlesListPage />
            </Route>
            <Route path="/articles/:name">
              <ArticlePage />
            </Route>
            <Route>
              <NotFoundPage />
            </Route>
          </Switch>
        </div>
      </div>
    </Router>
  );
}

export default App;
